import React, { useState, useEffect } from 'react';
import { Button, Form, Container, Header, Input } from 'semantic-ui-react';
import { Link } from 'gatsby';
import axios from 'axios';
import "../css/evalForm.css";
import { stripBasename } from 'history/PathUtils';
import { createBrowserHistory } from "history";
import '../css/addcourse.css';

export default function courseForm() {
    const [Course_id, setCourseId] = useState('');
    const [Course_name, setCourseName] = useState('');
    const [Course_term, setCourseTerm] = useState('');
    const [Course_year, setCourseYear] = useState('');
    const [Course_Start, setCourseStart] = useState('');
    const [Course_End, setCourseEnd] = useState('');
    const [Course_Day, setCourseDay] = useState('');

    const history = createBrowserHistory();
    
    const directToSuccess = () => {
        history.push('/coursesuccess');
        window.location.reload();
    };

    const handleSubmit = (e) => {
        const obj = { Course_id, Course_name, Course_term, Course_year, 
                        Course_Start, Course_End, Course_Day };

        axios
            .post(
                'https://sheet.best/api/sheets/4538c641-627e-42e1-b83a-78dfd288c3db',
                obj
            )
            .then((response) => {
                console.log(response);
            });

        directToSuccess();
    };

    return(
        <div class="container">
            <Container fluid className="container">
                <Header as="h3">Please fill out the following information for the new course.</Header>
                <div></div><br />

                <Form className="form">
                    <Form.Field inline>
                        <label><bold>Course ID  </bold></label>
                        <textarea id="Course_id" name="Course_id" rows="1" cols="50" onChange={(e) => setCourseId(e.target.value)}></textarea>
                    </Form.Field>
                    <div></div><br />

                    <Form.Field inline>
                        <label><bold>Course Name  </bold></label>
                        <textarea id="Course_name" name="Course_name" rows="1" cols="50" onChange={(e) => setCourseName(e.target.value)}></textarea>
                    </Form.Field>
                    <div></div><br />

                    <Form.Field inline>
                        <label><bold>Course Term  </bold></label>
                        <select name="Course_term" id="Course_term" onChange={(e) => setCourseTerm(e.target.value)}>
                            <option value="default" selected="true">Choose a term.</option>
                            <option>1</option>
                            <option>2</option>
                            <option>3A</option>
                            <option>3B</option>
                        </select>
                    </Form.Field>
                    <div></div><br />

                    <Form.Field inline>
                        <label><bold>Course Year</bold></label>
                        <textarea id="Course_year" name="Course_year" rows="1" cols="50" onChange={(e) => setCourseYear(e.target.value)}></textarea>
                    </Form.Field>
                    <div></div><br />

                    <Form.Field inline>
                        <label><bold>Start Time  </bold></label>
                        <input type="time" id="Course_start" name="Course_Start" min="8:00" max="20:00" required onChange={(e) => setCourseStart(e.target.value)}></input>
                    </Form.Field>
                    <div></div><br />

                    <Form.Field inline>
                        <label><bold>End Time  </bold></label>
                        <input type="time" id="Course_end" name="Course_End" min="8:00" max="20:00" required onChange={(e) => setCourseEnd(e.target.value)}></input>
                    </Form.Field>
                    <div></div><br />

                    <Form.Field inline>
                        <label><bold>Course Day(s)  </bold></label>
                        <select name="Course_day" id="Course_Day" onChange={(e) => setCourseDay(e.target.value)}>
                            <option value="default" selected="true">Select the day(s).</option>
                            <option>Monday/Wednesday/Friday</option>
                            <option>Monday/Wednesday</option>
                            <option>Monday</option>
                            <option>Wednesday</option>
                            <option>Tuesday/Thursday</option>
                            <option>Tuesday</option>
                            <option>Thurday</option>
                        </select>
                    </Form.Field>
                    <div></div><br />

                    <Button color="black" type='submit' onClick={handleSubmit}>Add Course</Button>
                </Form>
            </Container>
        </div>
    );
}